<template>
  <el-container class="main-container">
    <!-- 头部区域 -->
    <el-header>
      <!-- 左侧的 logo -->
      <img src="../../assets/images/logo.png" alt="" />
      <!-- 右侧的菜单 -->
      <el-menu
        class="el-menu-top"
        mode="horizontal"
        background-color="#23262E"
        text-color="#fff"
        active-text-color="#409EFF"
      >
        <el-submenu index="1">
          <template slot="title">
            <!-- 头像 -->
            <img v-if="$store.state.userInfo.user_pic" :src="$store.state.userInfo.user_pic" alt="" class="avatar" />
            <img v-else src="../../assets/images/logo.png" alt="" class="avatar" />
            <span>个人中心</span>
          </template>
          <el-menu-item index="1-1"
            ><i class="el-icon-s-operation"></i>基本资料</el-menu-item
          >
          <el-menu-item index="1-2"
            ><i class="el-icon-camera"></i>更换头像</el-menu-item
          >
          <el-menu-item index="1-3"
            ><i class="el-icon-key"></i>重置密码</el-menu-item
          >
        </el-submenu>
        <el-menu-item index="2" @click="loginOut"
          ><i class="el-icon-switch-button"></i>退出</el-menu-item
        >
      </el-menu>
    </el-header>
    <el-container>
      <!-- 侧边栏区域 -->
      <el-aside width="200px">
        <!-- 用户头像和欢迎区 -->
        <div class="user-box">
          <img :src="$store.state.userInfo.user_pic" alt="" v-if="$store.state.userInfo.user_pic" />
          <img src="../../assets/images/logoVue.png" alt="" v-else />
          <span>欢迎 {{ $store.state.userInfo.nickname || $store.state.userInfo.username }}</span>
        </div>
        <!-- 侧边栏导航菜单 -->
        <!-- router: 设置上（默认值为true），就会当你点击某行菜单的时候，以index值作为路由path切换 -->
        <el-menu
          :default-active="$route.path"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          background-color="#23262E"
          text-color="#fff"
          active-text-color="#409EFF"
          unique-opened
          router
        >
          <template v-for="item in menus">
            <el-menu-item
              :key="item.indexPath"
              v-if="!item.children"
              :index="item.indexPath"
            >
              <i :class="item.icon"></i>
              <span slot="title">{{ item.title }}</span>
            </el-menu-item>
            <el-submenu v-else :index="item.indexPath" :key="item.indexPath">
              <template slot="title">
                <i :class="item.icon"></i>
                <span>{{ item.title }}</span>
              </template>
              <el-menu-item
                :key="obj.indexPath"
                v-for="obj in item.children"
                :index="obj.indexPath"
                >
                <i :class="obj.icon"></i>
                <span>{{ obj.title }}</span></el-menu-item
              >
            </el-submenu>
          </template>
        </el-menu>
      </el-aside>
      <el-container>
        <!-- 页面主体区域 -->
        <el-main> <router-view></router-view> </el-main>
        <!-- 底部 footer 区域 -->
        <el-footer>© 版权所有</el-footer>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>
import { getMenusListAPI } from '@/api/index'
// 经验：在组件标签上绑定的所有事件（包括原生事件的名字click，input等等）
// 都是自定义事件，都需要组件内$emit来触发才行
// 万一组件不支持这个原生事件名字
// 解决方案:@事件名.native = "methods里方法名"
// .native给组件内根标签，绑定这个原生的事件
export default {
  name: 'my-layout',
  data () {
    return {
      // userInfo: this.$store.state.userInfo, // 这样写会导致更新vue，但是页面头像不会同步更新
      menus: [] // 保存侧边栏数据
    }
  },
  methods: {
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath)
    },
    loginOut () {
      this.$confirm('是否退出账号', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '手滑了',
        type: 'warning'
      })
        .then(() => {
          // 执行退出登录的操作
          // 1.清空token和用户信息
          this.$store.commit('updateToken', '')
          this.$store.commit('updateUserInfo', '')
          // 2.路由跳转到登录页
          this.$router.push('/login')
        })
        .catch((err) => {
          return err
        })
    },
    async getMenusListFn () {
      const res = await getMenusListAPI()
      this.menus = res.data.data
      console.log(this.menus)
    }
  },
  mounted () {
    // get用户信息不写在这里，写在vuex里（store/index.js）
    // getUseInforAPI().then(({ data: res }) => {
    //   this.userInfo = res
    // }).catch(err => err)
  },
  created () {
    this.getMenusListFn()
  }
}
</script>

<style lang="less" scoped>
.main-container {
  height: 100%;
  .el-header,
  .el-aside {
    background-color: #23262e;
  }
  .el-header {
    padding: 0;
    display: flex;
    justify-content: space-between;
  }
  .el-main {
    overflow-y: scroll;
    height: 0;
    background-color: #f2f2f2;
  }
  .el-footer {
    background-color: #eee;
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.avatar {
  border-radius: 50%;
  width: 35px;
  height: 35px;
  background-color: #fff;
  margin-right: 10px;
  object-fit: cover;
}
.user-box {
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  user-select: none;
  img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    background-color: #fff;
    margin-right: 15px;
    object-fit: cover;
  }
  span {
    color: white;
    font-size: 12px;
  }
}
.el-menu {
  border-right: 1px solid #000;
  user-select: none;
}
</style>
